



























































import {ConfirmType} from '@/modules/analyse/helpers/confirm';
import {Component, Vue} from 'vue-property-decorator';

@Component
export default class TheConfirmDialog extends Vue {

    public dialog: boolean = false;

    public resolve?: ((value?: any) => any);

    public reject?: ((value?: any) => any);

    public message?: string;

    public title?: string;

    public type?: ConfirmType;

    public data(): object {
        return {
            resolve: undefined,
            reject: undefined,
            message: undefined,
            title: undefined,
            type: undefined
        };
    }

    public open(title: string, type: ConfirmType, message?: string): Promise<boolean> {
        this.dialog = true;
        this.title = title;
        this.message = message ? message : undefined;
        this.type = type;
        return new Promise((resolve, reject) => {
            this.resolve = resolve;
            this.reject = reject;
        });
    }

    public agree() {
        this.dialog = false;
        setTimeout(() => {
            if (this.resolve) {
                this.resolve(true);
                this.$logger.debug(`${TheConfirmDialog.name} respond with ${true}`);
            }
        }, 300);
    }

    public cancel() {
        this.dialog = false;
        setTimeout(() => {
            if (this.resolve) {
                this.resolve(false);
                this.$logger.debug(`${TheConfirmDialog.name} respond with ${false}`);
            }
        }, 300);
    }

    get icon(): string | undefined {
        switch (this.type) {
            case ConfirmType.INFO:
                return 'info_outline';
            case ConfirmType.QUESTION:
                return 'help';
            case ConfirmType.DELETE:
                return 'delete';
            case ConfirmType.WARNING:
                return 'warning';
            case ConfirmType.ERROR:
                return 'error';
            case undefined:
                return undefined;
        }
    }

    get color(): string | undefined {
        switch (this.type) {
            case ConfirmType.INFO:
                return 'primary';
            case ConfirmType.QUESTION:
                return 'primary';
            case ConfirmType.DELETE:
                return 'error';
            case ConfirmType.WARNING:
                return 'warning';
            case ConfirmType.ERROR:
                return 'error';
            case null:
                return undefined;
        }
    }

    get isQuestion() {
        return this.type == ConfirmType.QUESTION
            || this.type == ConfirmType.DELETE;
    }
}
